html,
body {
  margin: 0;
  padding: 0;
}
body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.4em;
  background: #4d5b69;
  color: #4d5b69;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -ms-font-smoothing: antialiased;
  font-smoothing: antialiased;
  font-weight: 300;
  height: 100%;
}
ul {
  list-style: none;
  padding: 0;
}
.flex{
  display: flex;
}
.flex30{
  flex: 0 0 30%;
}
.flex40{
  flex: 0 0 40%;
}
.flex50{
  flex: 0 0 50%;
}
.flex60{
  flex: 0 0 60%;
}
.flex70{
  flex: 0 0 70%;
}
.flex1{
  flex: 1;
}
.flex-center{
  justify-content: center;
}
.pdl10{
  padding-left: 10px;
}
.pdl20{
  padding-left: 20px;
}
.pdt10{
  padding-top: .63rem;
}
.pdt20{
  padding-top: 1.13rem;
}
.mt10{
  margin-top: .63rem;
}
.mt20{
  margin-top: 1.13rem;
}
.posFull {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.posCenter {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.textXLarge {
  font-size: 2rem;
}
.textLarge {
  font-size: 1.5rem;
}
.textMedium {
  font-size: 1.125rem;
}
.textSmall {
  font-size: 0.875rem;
}
.textXSmall {
  font-size: 0.75rem;
}
.colorTextLight{
  color: #fff;
}
.colorTextDard{
  color: rgb(67, 68, 86);
}
.colorTextDard1{
  color: rgb(128, 145, 171);
}
.formInput {
  display: block;
  width: 100%;
  flex: 1;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #d3e5f7;
  background-color: #4d5b69;
  background-clip: padding-box;
  border: none;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.formTextarea {
  width: 100%;
  flex: 1;
  height: 120px;
  padding: 1rem;
  font-size: 1rem;
  font-weight: 400;
  color: #d3e5f7;
  background-color: #4d5b69;
  background-clip: padding-box;
  border: none;
  resize: none;
}
.container {
  position: absolute;
  width: 100%;
  height: 100%;
 }
.main {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  padding-left: 200px;
  background-color: #4d5b69;
}
.module {
  display: flex;
  padding: 20px;
  box-sizing: border-box;
}
.module .title {
  padding: 15px 20px;
  background-color: #283847;
}
.module .content {
  padding: 20px;
  color: #a7b2ae;
  background-color: #2f4050;
}
.module .title > h3 {
  font-size: 18px;
  color: #79fded;
  margin-bottom: 0;
}
.inputWrap {
  display: flex;
  padding: 15px 0;
}
.inputWrap .label {
  width: 100px;
  line-height: 38px;
  font-size: 15px;
  font-weight: 500;
}
/* bootstrap */
.table-dark {
  color: #fff;
  background-color: #2e4050 !important;
}
.table-dark td {
  max-width: 200px;
}
.table-dark td.thumb-box {
  width: 120px;
  height: 100px;
}
.page-link {
  color: #fff;
  background-color: #394959;
  border: 1px solid #454d55;
}
.page-link:hover {
  color: #fff;
  border: 1px solid #17a2b8;
  background-color: #17a2b8;
}
.page-item.active .page-link {
  background-color: #17a2b8;
  border-color: #17a2b8;
}
.btn {
  border-radius: 0;
}
